import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { DirectReviewTree } from 'app/pages/tzb/custom/marketing/direct-review/direct-review';

export const routes: Routes = [
	{
		path: '',
		children: [
			{ path: 'marketing-project', loadChildren: './marketing-project/marketing-project.module#MarketProjectModule' },//方案
			{ path: 'marketing-activite', loadChildren: './marketing-activite/marketing-activite.module#MarketingActiviteModule' },//活动
			{ path: 'my-activite-project', loadChildren: './my-activite-project/my-activite-project.module#MyProjectModule' },//我的营销
			{ path: 'cust-query', loadChildren: './cust-query/cust-query.module#CustQueryModule' },//我的营销	
			{ path: 'voucher', loadChildren: './voucher/voucher.module#VoucherModule' },	
			{ path: 'marketing-content-manage', loadChildren: './marketing-content-manage/marketing-content-manage.module#MarketingContentManageModule' },	
			{ path: 'direct-review', component: DirectReviewTree }, // 复核面面
		]
	}

]

export const MarketingRouting: ModuleWithProviders = RouterModule.forChild(routes);
